
import { defineComponent, computed, ref } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { useStore } from '@/store'
export default defineComponent({
  setup() {
    const store = useStore()

    const changeColor = (item: any) => {
      const index = messageList.value.findIndex((item2: any) => item2.id === item.id)
      messageList.value[index].isRed = !messageList.value[index].isRed
      if (messageList.value[index].isRed) {
        messageList.value[index].praiseCount++
      } else {
        messageList.value[index].praiseCount--
      }
    }

    // 分页
    // const queryInfo = pageInfo.value
    const pageInfo = ref({
      pageNum: 1,
      pageSize: 6
    })
    const queryInfo = { pageNum: 1, pageSize: 6 }

    store.dispatch('message/getMessageListAction', { pageInfo })
    const messageList = computed(() => store.getters['message/messageListData'])
    const total = computed(() => store.getters[`message/messageListCount`])
    const handleSizeChange = (pageSize: any) => {
      pageInfo.value.pageSize = pageSize
      store.dispatch('message/getMessageListAction', {
        pageInfo
      })
    }
    const handleCurrentChange = (pageNum: any) => {
      pageInfo.value.pageNum = pageNum
      store.dispatch('message/getMessageListAction', {
        pageInfo
      })
    }
    const deleteContent = async (item: any) => {
      const id = item.id
      const res = await store.dispatch('message/deleteDataAction', { id, pageInfo })
      if (!res.success) {
        return ElMessage({
          message: '删除留言失败！',
          type: 'error'
        })
      }
      ElMessage({
        message: '删除留言成功！',
        type: 'success'
      })
    }
    // 编辑
    const open = (item: any) => {
      ElMessageBox.prompt('请输入内容', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      })
        .then(({ value }) => {
          ElMessage({
            type: 'success',
            message: '编辑成功'
          })
          const editInfo = {
            id: item.id,
            userName: item.userName,
            userId: item.userId,
            userMessage: value,
            praiseCount: item.praiseCount
          }
          store.dispatch('message/editMessageDataAction', { editInfo, pageInfo })
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '取消输入'
          })
        })
    }
    return {
      changeColor,
      deleteContent,
      open,
      pageInfo,
      messageList,
      total,
      handleSizeChange,
      handleCurrentChange,
      queryInfo
    }
  }
})
